<template>
  <v-container :style="{width:'100%'}">
    <v-app>
      <hooper :vertical="true" :center-mode="true" :style="{backgroundColor:'#8DBDE5'}">
        <slide>
          <First @change-page="changePage" :style="{backgroundSize: '100%', backgroundRepeat:'no-repeat' ,backgroundImage: 'url(' + require('@/assets/main/bg_01main_01.jpg') + ')',  width:'100%', height:'auto' }"/>
        </slide>
        <slide>
          <Second @change-page="changePage" :style="{backgroundSize: '100%', backgroundRepeat:'no-repeat' ,backgroundImage: 'url(' + require('@/assets/main/bg_01main_02.jpg') + ')',  width:'100%', height: 'auto' }"/>
        </slide>
        <slide>
          <Third :style="{backgroundSize: '100%', backgroundRepeat:'no-repeat' ,backgroundImage: 'url(' + require('@/assets/main/bg_01main_03.jpg') + ')',  width:'100%', height: 'auto' }"/>
        </slide>
      </hooper>
    </v-app>
  </v-container>
</template>

<script>
import {
  Hooper,
  Slide
} from 'hooper';
import 'hooper/dist/hooper.css';
export default {
  name: "Main",
  components:{
    First:()=>import('@/components/main/First'),
    Second:()=>import('@/components/main/Second'),
    Third:()=>import('@/components/main/Third'),
    Hooper, Slide
  },
  created() {
  },
  data:()=>({
    text:'메인 페이지',
    first:{
    },
    second:{
    },
    third:{
    }
  }),
  mounted() {
    document.addEventListener('scroll', this.handleScroll)
  },
  destroyed() {
    document.removeEventListener('scroll', this.handleScroll)
  },
  methods:{
    handleScroll(){
      console.log(window.scrollY)
    },
    changePage(menu){
      console.log('Main > '+menu)
      this.$emit('change-page',menu,event)
    }
  }
}
</script>

<style scoped>
.container {
  max-width: 100%;
}
</style>